
/*Hero Styles*/

.hero {
	position: relative;
	justify-content: center;
	text-align: center;
	min-height: 100vh;
	color: #fff;

	.background-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	z-index: -1;
	background-color: #80a3db;
}
	.background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	z-index: -1;
	background-color: #80a3db;
	display:none;
}


	h1 {
	font: bold 60px "Open Sans", sans-serif;
	margin-bottom: 15px;
}

	 h3 {
	font: normal 28px "Open Sans", sans-serif;
	margin-bottom: 40px;
}

	a.btn {
	padding: 20px 46px;
}

	.hero-content-area {
	opacity: 0;
	margin-top: 100px;
	animation: 1s slidefade 1s forwards;
}

@keyframes slidefade {
	100% {
		opacity: 1;
		margin: 0;
	}
}

@media (max-width: 800px) {
	.hero{
		min-height: 600px;
	}

	h1 {
		font-size: 48px;
	}

	h3 {
		font-size: 24px;
	}

    a.btn {
		padding: 15px 40px;
	}
}

@media  (max-width: 500px) {
	.background-image {
		display:none;
	}
	.background {
		display: block;
	}
}

}

